
<template>
    <div>
        <mars-view style="backgroundColor: null;"><mars-view v-if="info" class="character-wrap"><mars-view> name: {{info.name}} </mars-view> <mars-view> gender: {{info.gender}} </mars-view> <mars-view> culture: {{info.culture}} </mars-view> <mars-view> born: {{info.born}} </mars-view> <mars-view v-if="info.titles && info.titles.length > 0 && info.titles[0]">
            titles: <mars-text v-for="(item, index) in info.titles" :key="index">[ {{item}} ]</mars-text></mars-view> <mars-view v-if="info.aliases && info.aliases.length > 0 && info.aliases[0]">
            aliases: <mars-text v-for="(item, index) in info.aliases" :key="index">[ {{item}} ]</mars-text></mars-view></mars-view></mars-view>
    </div>
</template>

<script>


export default {
  data() {
    return {
      info: null
    };
  },

  beforeMount() {
    (function (options) {
      const characterID = options.id || 148;

      this.$api.request({
        url: `https://anapioficeandfire.com/api/characters/${characterID}`,
        method: 'GET',
        mode: undefined,
        credentials: 'same-origin'
      }).then(res => {
        const {
          name,
          gender,
          culture,
          born,
          titles,
          aliases
        } = res.data;
        this.info = {
          name: name || 'unknown',
          gender,
          culture: culture || 'unknown',
          born: born || 'unknown',
          titles,
          aliases
        };
      });
    }).call(this, this.$route.query);
  }

};
</script>


<style lang="less" scoped>
    
.character-wrap {
    width: 100vw;
    height: 100vh;
    padding: 30px;
    box-sizing: border-box;

    div {
        font-size: 40px;
        margin-bottom: 20px;

        span {
            font-size: 36px;
            color: #555;
            margin-right: 25px;
        }
    }
}

</style>
