
<template>
    <div>
        <mars-view class="book-detail" style="backgroundColor: null;"><book :poster="poster" v-if="poster" class="book-common book-poster"></book> <mars-view class="book-common" v-if="info"><mars-view>name: {{info.name}}</mars-view> <mars-view>released: {{info.released}}</mars-view> <mars-view>author: {{info.authors[0]}}</mars-view> <mars-view>publisher: {{info.publisher}}</mars-view></mars-view> <mars-view class="characters" v-if="characters.length > 0"><mars-view>An array of Character that has been in this book.</mars-view> <mars-view v-for="(item, index) in characters" :key="index"><mars-navigator :url="item" class="role-item">角色 {{index + 1}} 介绍 </mars-navigator></mars-view></mars-view></mars-view>
    </div>
</template>

<script>

import Book from "../../components/Book/index.vue";
import { requireSrc } from '../../js/utils';

export default {
  data() {
    return {
      poster: '',
      info: null,
      characters: []
    };
  },

  components: {
    book: Book
  },

  beforeMount() {
    (function (options) {
      const bookid = options.bookid;
      this.poster = requireSrc(`../../images/book${bookid}.jpg`);

      this.$api.request({
        url: `https://anapioficeandfire.com/api/books/${bookid}`,
        method: 'GET',
        mode: undefined,
        credentials: 'same-origin'
      }).then(res => {
        const {
          name,
          publisher,
          released,
          authors,
          characters
        } = res.data;
        this.info = {
          name,
          publisher,
          released,
          authors
        };
        characters.slice(0, 10).forEach(item => {
          const index = item.lastIndexOf('/');
          this.characters.push(`/pages/character/index?id=${item.slice(index + 1)}`);
        });
      });
    }).call(this, this.$route.query);
  }

};
</script>


<style lang="less" scoped>
    
.book-detail {
    padding: 30px;
    .book-common {
        display: block;
        float: left;
        width: 40%;
        height: 400px;
    }
    .book-poster {
        margin-right: 5%;
    }
    .characters {
        clear: both;
        padding-top: 50px;
        font-size: 32px;
        .role-item {
            text-align: left;
            margin-bottom: 30px;
        }
    }
}


</style>
